import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormControl, Validators } from '@angular/forms';
import { readFileAsBase64 } from '@utils/read-file-as-base-64';
import { URLS } from '@enums/urls';
import { AppStateInterface } from '@store/app/app-state.interface';
import { Store } from '@ngrx/store';
import { selectCurrentUserProfileName } from '@store/auth/auth.selectors';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-new-artwork',
  templateUrl: 'new-artwork.component.html',
  styleUrls: ['new-artwork.component.scss'],
})
export class NewArtworkComponent implements OnInit {
  isDragover: boolean = false;
  file: File | null = null;
  base64String: string | null = null;
  maxLengthDescription: number = 5000;
  userName$: Observable<string | null> = this.store.select(selectCurrentUserProfileName);

  constructor(
    private store: Store<AppStateInterface>,
    private fb: FormBuilder,
  ) {}

  newArtworkForm = this.fb.group({
    artists: this.fb.array([this.fb.control('')]),
    title: new FormControl('', [Validators.required]),
    description: new FormControl('', [Validators.required]),

    technique: new FormControl(''),
    dimensions: new FormControl(''),
    material: new FormControl(''),
    preservation: new FormControl(''),
    location: new FormControl(''),
    others: new FormControl(''),

    dateFormatType: new FormControl(''),
    afterChrist: new FormControl(false),
    isArtistUser: new FormControl(false, [Validators.required]),
    agreeToTerms: new FormControl(false, [Validators.required]),
    price: new FormControl(0),
  });

  get artists() {
    return this.newArtworkForm.controls['artists'] as FormArray;
  }

  ngOnInit() {
    this.newArtworkForm.get('isArtistUser')?.valueChanges.subscribe((value) => {
      if (value) {
        this.userName$.subscribe((username) => {
          if (username) {
            this.newArtworkForm.controls['artists'].controls[0].setValue(username);
          }
        });
      } else {
        this.newArtworkForm.controls['artists'].controls[0].setValue('');
      }
    });
  }

  onSubmit() {
    const formValue = this.newArtworkForm.getRawValue();
    console.log(formValue);
  }

  skipImage() {
    this.file = null;
  }

  addArtist() {
    this.artists.push(this.fb.control(''));
  }

  deleteArtist(artistIndex: number) {
    this.artists.removeAt(artistIndex);
  }

  async onFileSelected(event: Event) {
    const target = event.target as HTMLInputElement;
    const fileList: FileList | null = target.files;
    if (fileList && fileList.length > 0) {
      this.file = fileList[0];
      this.base64String = await readFileAsBase64(fileList[0] as File);
      console.log('file from input', this.file);
    }
  }

  async storeFile($event: Event) {
    this.isDragover = false;
    this.file = ($event as DragEvent).dataTransfer
      ? ($event as DragEvent).dataTransfer?.files.item(0) ?? null
      : ($event.target as HTMLInputElement).files?.item(0) ?? null;
    console.log('file from drag', this.file);
    this.base64String = await readFileAsBase64(this.file as File);
  }

  protected readonly URLS = URLS;
}
